
.con {
    background-color: #EBCCDC;
}
.pfp {
    display: flex;
    margin-left: auto;
    margin-right: auto;
    padding-top: 12%;
}

.name{
    text-align: center;
    font-size: 40px;
    padding-top: 3%;
    padding-bottom: 3%;
    color: #744B60;
    font-family: 'Nunito', sans-serif;
}

.links {
    display: flex;
    padding-top: 5%;
    padding-bottom: 5%;
}

.links > *{
    margin-left: 9%;
    margin-right: 9%;
}

@media screen and (max-width:768px) {
    .linkedin, .git {
        width: 80%;
    }

    .mail {
        width: 85%;
        height: 85%;
    }

    .pfp {
        width: 20%;
    }

    .name{
        font-size: 25px;
        padding-top: 3%;
        padding-bottom: 3%;
    }


}


@media only screen and (device-width: 626px) {

    .links > *{
        margin-left: 10%;
        margin-right: 10%;
    }
    
    .name{
        font-size: 27px;
        padding-top: 5%;
        padding-bottom: 3%;
    }
  
  }
